<template>
  <div class="login-page flex flex-col justify-center gap-6">
    <div class="flex-1 flex flex-col items-center gap-6 justify-center">
      <Card :style="{ maxWidth: '420px' }" class="rounded-xl w-full px-6 py-4">
        <div class="space-y-16">
          <div class="flex justify-center items-center flex-col">
            <img src="@/assets/logo.svg" alt="Brand logo" class="mb-2" width="180" whitelabel="image-replace" data-image-type="logo">
            <p class="text-dark">Login into your dashboard</p>
          </div>
          <div class="form">
            <form class="space-y-5" @submit.prevent="loginSubmit">
              <div>
                <Input placeholder="Email Address" name="email" id="email" v-model="data.email" :view="validation.email.$invalid && 'danger' || 'default'" block />
                <span v-for="(error, n) of validation.email.$errors" :key="n" class="text-red-base mt-2 block">
                  {{ error.$message }}.
                </span>
              </div>
              <div>
                <Input placeholder="Password" name="password" id="password" v-model="data.password" :type="showPassword ? 'text' : 'password'" :view="validation.password.$invalid && 'danger' || 'default'" block icon right>
                  <template #icon>
                    <span class="material-icons block cursor-pointer text-grey-03" @click="showPassword = !showPassword">{{ showPassword ? 'visibility_off' : 'visibility' }}</span>
                  </template>
                </Input>
                <span v-for="(error, n) of validation.password.$errors" :key="n" class="text-red-base mt-2 block">
                  {{ error.$message }}.
                </span>
              </div>
              <div class="flex items-center">
                <Checkbox type="default" class="text-grey-02 font-normal" v-model="data.is_remember" :value="1" :value-off="0">Remember me</Checkbox>
                <router-link class="ml-auto" :to="{ name: 'forgotPassword' }">Forgot Password</router-link>
              </div>
              <div>
                <Button view="filled" color="teal" type="submit" :loading="loading" block>Log in</Button>
              </div>
            </form>
          </div>
        </div>
      </Card>
      <div>
        Don't have an account?
        <a @click.prevent="handleRegister" rel="noreferrer noopener">Sign up</a>
      </div>
    </div>
    <div whitelabel="remove" class="py-4">
      <div class="flex items-center justify-center gap-4">
        <a href="https://www.reusely.com/privacy-policy" rel="noreferrer noopener" class="hover:underline text-grey-03 hover:text-grey-03" target="_blank">Privacy</a>
        <a href=" https://www.reusely.com/terms-of-service" rel="noreferrer noopener" class="border-l border-r px-4 border-grey-02 hover:underline text-grey-03 hover:text-grey-03" target="_blank">Terms</a>
        <a href="https://help.reusely.com" rel="noreferrer noopener" target="_blank" class="cursor-pointer hover:underline text-grey-03 hover:text-grey-03">
          Help
        </a>
      </div>
      <p class="text-center mt-4" whitelabel="text-replace" data-label="copy right">Â© 2022 Reusely.com</p>
    </div>
  </div>
  <Modal v-model="modal.two_factor_auth" size="xs" hide-footer @close="handleUnsetTwoFactorAuth">
    <template #default>
       <div class="flex flex-wrap flex-col items-center text-center mb-6">
        <img class="mb-6" src="@/assets/icon/verify-phone.svg" alt="verify phone number icon">
        <h6 class="mb-2">Verify Phone Number</h6>
        <p class="text-grey-04">A text message with a 6-digit verification code was just <br/> sent to {{verifiedPhoneNumber}}</p>
      </div>
      <div class="mb-8">
        <Input v-model="data.phone_code" v-maska="'######'" class="mb-8" label="Enter Your Verification Code" block/>
        <Button @click="loginSubmit" color="teal" block :disabled="!data.phone_code" :loading="loading">Verify and Log In</Button>
      </div>
      <div class="text-center mb-8">
        <p class="text-sm text-grey-03">It may take a minute to receive your code. <br/> Haven't received it? <a :class="['resend-code', loadingResendTwoFactorAuth || isDisableResend && '--loading']" @click.prevent="handleResendVerificationCode">Resend a new code<span class="text-grey-02">{{isDisableResend ? ` (${resendCountdownTimer})` : ''}}</span>.</a></p>
      </div>
    </template>
  </Modal>
</template>

<script>
import { ref, defineComponent, onMounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useLogin } from 'composable/user'
import { isWhiteLabel } from 'composable/whitelabel'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import { useVuelidate } from '@vuelidate/core'
import { required, email } from '@/validations/index'
import { ElMessage } from 'element-plus'
import ChangelogEmbed from 'components/ChangelogEmbed/ChangelogEmbed.vue'
import ChangelogEmbedStatic from 'components/ChangelogEmbed/ChangelogEmbedStatic.vue'

import { initNewSession } from 'utils/session'
import { useFeatureFlag } from 'composable/whitelabel'
import { content } from 'constants/whiteLabels'

export default defineComponent({
  name: 'Login',
  components: {
    Button,
    Card,
    Input,
    Checkbox,
    Modal,
    ChangelogEmbed,
    ChangelogEmbedStatic
  },
  setup() {
    const route = useRoute()
    const router = useRouter()
    const { resendCountdownTimer, data, loading, loadingResendTwoFactorAuth, modal, resendTwoFactorAuthCode, login, setResendCountdownTimer, unsetResendCountdownTimerdata } = useLogin()
    const showPassword = ref(false)
    const verifiedPhoneNumber = ref('')
    const isDisableResend = computed(() => resendCountdownTimer.value !== 0)
    const loginSubmit = () => {
      const ghlPathToken = JSON.parse(localStorage.getItem('goHighLevelToken'))
      if(validate()) {
        login(async (response) => {
          if(response.errors && response.errors.connection_name) {
            if(response.errors.is_dual_auth_veification == 1){
              modal.two_factor_auth = true
              verifiedPhoneNumber.value = response.errors.phone_number
              return
            }
            window.localStorage.setItem('connection_name', response.errors.connection_name)
            window.localStorage.setItem('registration_email', response.errors.email)
            router.push({
              name: 'verify'
            })
          }
          else {
            // Set Token User
            window.localStorage.setItem('token', response.token)
            initNewSession()

            if (!!ghlPathToken) {
              return router.push({
                path: ghlPathToken.path,
                query: ghlPathToken.query
              })
            }

            router.push({
              path: route.query.redirect || '/dashboard',
            })
          }
        })
      }
    }
    const rules = {
      email: { required, email },
      password: { required }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleResendVerificationCode = async () => {
      if(!loadingResendTwoFactorAuth.value && !isDisableResend.value){
        await resendTwoFactorAuthCode(() => {
          setResendCountdownTimer(60)
          ElMessage({
            type: 'success',
            message: `The verification code is being sent to ${verifiedPhoneNumber.value}`
          })
        })
      }
    }
    const handleUnsetTwoFactorAuth = () => {
      data.phone_code = null
      unsetResendCountdownTimerdata()
    }
    const handleRegister = () => {
      if (!!isWhiteLabel.value && !!content[window.whiteLabel.company]?.register_link) {
        return window.open(content[window.whiteLabel.company].register_link)
      }

      window.open(
        `${window.location.origin}/onboarding/1?plan=starter`,
        '_self',
        'noopener noreferrer'
      )
    }
    
    onMounted(() => {
      const isExpired = route.query.expired == 1
      if(isExpired){
        router.push({
          name: 'login'
        })
        ElMessage({
          type: 'warning',
          message: 'Your session has expired'
        })
      }
    })
    return {
      data,
      loading,
      loadingResendTwoFactorAuth,
      modal,
      showPassword,
      verifiedPhoneNumber,
      resendCountdownTimer,
      isWhiteLabel,
      isDisableResend,
      loginSubmit,
      handleResendVerificationCode,
      handleUnsetTwoFactorAuth,
      handleRegister,
      validation,
    }
  },
})
</script>
<style lang="scss">
  .resend-code{
    @apply select-none;
    &.--loading{
      @apply text-grey-01 cursor-not-allowed;
    }
  }
  .login-page {
    @apply h-screen;
  }
  @media screen and (max-height: 540px) {
    .login-page {
      @apply h-auto;
    }
  }
</style>